import React from 'react'
import { useState } from 'react'
import {Button,Collapse,Row,Col} from 'reactstrap'

function Slider() {
    const [aboutCollapse, setAboutCollapse] = useState(false);
    const [newCollapse, setNewCollapse] = useState(false);

    return (
        <div className="slider">
            <div className="py-3"  style={{backgroundImage : "url(https://source.unsplash.com/random/1920x1080)"}}>
               <h1 className="text-white text-shadow py-3">My First Github Website with full of my project</h1>
               <div>
                   <Row>
                       <Col md="5" >
                           
                       
                <Button
                className="m-3"
                color='primary'
                    onClick={function noRefCheck(){
                        setAboutCollapse(!aboutCollapse)
                    }}
                    style={{
                    marginBottom: '1rem',
                   
                    width : 300
                    }}
                    outline
                >
                Components <i class="bi bi-caret-down"></i>
                </Button>
                <Collapse className="text-white" isOpen={aboutCollapse}>
                <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                </Collapse>
                </Col>
                <Col md="5">
                    
                
                <Button
                className="m-3"
                color='primary'
                    onClick={function noRefCheck(){
                        setNewCollapse(!newCollapse)
                    }}
                    style={{
                    marginBottom: '1rem',
                    width : 300
                    }}
                    outline
                >
                What's New <i class="bi bi-caret-down"></i>
                </Button>
                <Collapse className="text-white" isOpen={newCollapse}>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                    <Row>
                        <Col >
                        list    
                        </Col>
                        
                    </Row>
                </Collapse>
                </Col>
                </Row>
</div>

            </div>
        </div>
    )
    
}

export default Slider
