import React from 'react';
import {Editor, ConvertFromRaw, ContentState, EditorState, convertFromRaw} from 'draft-js';
import { Container } from 'reactstrap';

const Post = (props) => {
    const RawDraftContentState = JSON.parse(localStorage.getItem("savedContent"))
   const content = {content : EditorState.createWithContent(
       convertFromRaw(RawDraftContentState)
   )}
  console.log(RawDraftContentState);
  return (
     <Container className="py-3">
       <Editor editorState={content.content} readOnly={true} />
       </Container>
  );
}

export default Post