import React from 'react'
import {Card,CardTitle,CardBody, Row, Col, CardImg} from 'reactstrap'

function MetaPost(props) {
    const posts = props.post
    return (
        <div>
       
                
           <Row className="py-3 my-3">
              
                {posts.map((post)=>{
                return (
                    <Col className="py-3">
                    <Card style={{width:400}}>
                        <CardImg src={post.src} alt="" />
                
                <CardTitle>
                    {post.title}
                </CardTitle>
                <CardBody>
                    {post.desc} <br />
                    <a href="/">Read More...</a>
                </CardBody>
                
            </Card>

             </Col>
                )
                })
            }
          
            
            </Row>
        </div>
    )
}

export default MetaPost
