import React from 'react'
import { Col,Row,Container } from 'reactstrap'
import NodeJsLogo from './logo/NodeJs.png'
import pythonLogo from './logo/python.png'
import htmlLogo from './logo/html5.png'
import cssLogo from './logo/css.png'
import bootstrapLogo from './logo/bootstrap.png'
import reactLogo from './logo/ReactJs.png'

function Tecnology() {
    const Tech = [{
        Name : "HTML5",
        src : htmlLogo
    },
    {
        Name : "CSS",
        src : cssLogo
    },
    {
        Name : "Javascript",
        src : NodeJsLogo
    },
    {
        Name : "Python",
        src : pythonLogo
    },
    {
        Name : "Bootstrap",
        src : bootstrapLogo
    },
    {
        Name : "ReactJs",
        src : reactLogo
    },]
    return (
        <Container fluid className="p-3" >
            <Row>
            {
                Tech.map((t) => {
                    return <Col sm>
                        <div style={{height:250}} className="p-3 mb-3">
                    <img src={t.src} style={{position: 'relative',height:100}} alt="" />
                    <br />
                    <span className="fw-bold font-monospace">{t.Name}</span>
                    </div>
                    </Col>
                })
            }
            </Row>
        </Container>
    )
}

export default Tecnology
