import React from 'react'
import { Nav,NavItem,NavLink,NavbarBrand,DropdownMenu,UncontrolledDropdown,DropdownToggle,Collapse,DropdownItem,NavbarText,Navbar,NavbarToggler, Button } from 'reactstrap'
import { Link } from "react-router-dom"
function Navigation() {
    return (
        <div>
        <div>
  <Navbar
    color="light"
    expand="md"
    light
  >
    <NavbarBrand href="/">
      J-Esex
    </NavbarBrand>
    <NavbarToggler onClick={function noRefCheck(){}} />
    <Collapse navbar>
      <Nav
        className="me-auto"
        navbar
      >
        <NavItem>
          <NavLink href="/components/">
            Components
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink href="https://github.com/reactstrap/reactstrap">
            GitHub
          </NavLink>
        </NavItem>
        <UncontrolledDropdown
          inNavbar
          nav
        >
          <DropdownToggle
            caret
            nav
          >
            Options
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem>
              Option 1
            </DropdownItem>
            <DropdownItem>
              Option 2
            </DropdownItem>
            <DropdownItem divider />
            <DropdownItem>
              Reset
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      </Nav>
      <NavbarText>
        <Button
        color="primary" outline>
          <Link to="createPost">Create Post</Link>
        </Button>
      </NavbarText>
    </Collapse>
  </Navbar>
</div>
        </div>
    )
}

export default Navigation
